import {
  BackgroundFilterBottomLeft,
  BackgroundFilterTopRight,
  BackgroungStarSmall,
  CurrentPage,
  Logo,
  NextPageBtn,
  PageCounter,
  Pagination,
  PreviousPageBtn,
  Question,
  QuizArrowLeft,
  QuizArrowRight,
  QuizBox,
  QuizButton,
  QuizButtonBox,
  QuizButtonContent,
} from '../Quiz.styled';

export const QuizQuestionAge = ({
  activeSlide,
  isChild,
  continueQuiz,
  previousQuestion,
  nextQuestion,
  quizValues,
  lang
}) => {
  const setQuizValue = (e, value) => {
    quizValues.current.age = value;
    continueQuiz(e);
  };

  return (
    <>
      <QuizBox>
        <Logo />
        <Question>
          {isChild ? '–í–∫–∞–∂—ñ—Ç—å –≤—ñ–∫ –¥–∏—Ç–∏–Ω–∏' : '–í–∫–∞–∂—ñ—Ç—å –≤–∞—à –≤—ñ–∫'}
        </Question>
        <QuizButtonBox>
          <QuizButton
            onClick={e =>
              setQuizValue(e, isChild ? '–¥–æ 7 —Ä–æ–∫—ñ–≤' : '–¥–æ 18 —Ä–æ–∫—ñ–≤')
            }
            className={
              (quizValues.current?.age === '–¥–æ 7 —Ä–æ–∫—ñ–≤' ||
                quizValues.current?.age === '–¥–æ 18 —Ä–æ–∫—ñ–≤') &&
              'chosen'
            }
          >
            <QuizButtonContent>
              {isChild ? '–¥–æ 7 —Ä–æ–∫—ñ–≤' : '–¥–æ 18 —Ä–æ–∫—ñ–≤'}
            </QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, isChild ? '7-9 —Ä–æ–∫—ñ–≤' : '18-24 —Ä–æ–∫–∏')}
            className={
              (quizValues.current?.age === '7-9 —Ä–æ–∫—ñ–≤' ||
                quizValues.current?.age === '18-24 —Ä–æ–∫—ñ–≤') &&
              'chosen'
            }
          >
            <QuizButtonContent>
              {isChild ? '7-9 —Ä–æ–∫—ñ–≤' : '18-24 —Ä–æ–∫–∏'}
            </QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e =>
              setQuizValue(e, isChild ? '9-11 —Ä–æ–∫—ñ–≤' : '25-35 —Ä–æ–∫—ñ–≤')
            }
            className={
              (quizValues.current?.age === '9-11 —Ä–æ–∫—ñ–≤' ||
                quizValues.current?.age === '25-35 —Ä–æ–∫—ñ–≤') &&
              'chosen'
            }
          >
            <QuizButtonContent>
              {isChild ? '9-11 —Ä–æ–∫—ñ–≤' : '25-35 —Ä–æ–∫—ñ–≤'}
            </QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e =>
              setQuizValue(
                e,
                isChild ? '12 —Ä–æ–∫—ñ–≤ —ñ –±—ñ–ª—å—à–µ' : '35 —Ä–æ–∫—ñ–≤ —ñ –±—ñ–ª—å—à–µ'
              )
            }
            className={
              (quizValues.current?.age === '12 —Ä–æ–∫—ñ–≤ —ñ –±—ñ–ª—å—à–µ' ||
                quizValues.current?.age === '35 —Ä–æ–∫—ñ–≤ —ñ –±—ñ–ª—å—à–µ') &&
              'chosen'
            }
          >
            <QuizButtonContent>
              {isChild ? '12 —Ä–æ–∫—ñ–≤ —ñ –±—ñ–ª—å—à–µ' : '35 —Ä–æ–∫—ñ–≤ —ñ –±—ñ–ª—å—à–µ'}
            </QuizButtonContent>
          </QuizButton>
        </QuizButtonBox>
        <BackgroundFilterTopRight />
        <BackgroundFilterBottomLeft />
        <BackgroungStarSmall />
        <Pagination>
          <PreviousPageBtn
            className={activeSlide - 1 < 1 && 'disabled'}
            disabled={activeSlide - 1 < 1 && true}
            onClick={previousQuestion}
          >
            <QuizArrowLeft />
          </PreviousPageBtn>
          <PageCounter>
            <CurrentPage>{activeSlide}</CurrentPage>/{lang ? 7 : 8}
          </PageCounter>
          <NextPageBtn
            className={
              activeSlide + 1 > 1 && !quizValues.current?.age && 'disabled'
            }
            disabled={activeSlide + 1 > 1 && !quizValues.current?.age && true}
            onClick={nextQuestion}
          >
            <QuizArrowRight />
          </NextPageBtn>
        </Pagination>
      </QuizBox>
    </>
  );
};
