import book from '../../../img/quiz/book.png';
import brain from '../../../img/quiz/brainmelt.png';
import speak from '../../../img/quiz/speak.png';
import ear from '../../../img/quiz/ear.png';

import {
  BackgroundFilterBottomLeft,
  BackgroundFilterTopRight,
  BackgroungStarSmall,
  CurrentPage,
  Emoji,
  Logo,
  NextPageBtn,
  PageCounter,
  Pagination,
  PreviousPageBtn,
  Question,
  QuizArrowLeft,
  QuizArrowRight,
  QuizBox,
  QuizButton,
  QuizButtonBox,
  QuizButtonContent,
} from '../Quiz.styled';

export const QuizQuestionDifficulties = ({
  activeSlide,
  continueQuiz,
  previousQuestion,
  nextQuestion,
  quizValues,
  lang
}) => {
  const setQuizValue = (e, value) => {
    quizValues.current.difficulties = value;
    continueQuiz(e);
  };

  return (
    <>
      <QuizBox>
        <Logo />
        <Question>
          –Ø–∫—ñ —Ç—Ä—É–¥–Ω–æ—â—ñ —É –≤–∞—Å –≤–∏–Ω–∏–∫–∞—é—Ç—å –ø—ñ–¥ —á–∞—Å –≤–∏–≤—á–µ–Ω–Ω—è {quizValues.current.lang === 'en'
            ? '–∞–Ω–≥–ª—ñ–π—Å—å–∫–æ—ó'
            : quizValues.current.lang === 'de'
            ? '–Ω—ñ–º–µ—Ü—å–∫–æ—ó'
            : quizValues.current.lang === 'pl'
            ? '–ø–æ–ª—å—Å—å–∫–æ—ó'
            : '—ñ–Ω–æ–∑–µ–º–Ω–æ—ó'} –º–æ–≤–∏?
        </Question>
        <QuizButtonBox>
          <QuizButton
            onClick={e => setQuizValue(e, '–ù–µ–∑–Ω–∞–π–æ–º—ñ —Å–ª–æ–≤–∞')}
            className={
              quizValues.current?.difficulties === '–ù–µ–∑–Ω–∞–π–æ–º—ñ —Å–ª–æ–≤–∞' && 'chosen'
            }
          >
            <QuizButtonContent>
              <Emoji src={book} alt="Vocabulary emoji" width="21" />
              –ù–µ–∑–Ω–∞–π–æ–º—ñ —Å–ª–æ–≤–∞
            </QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, '–ì—Ä–∞–º–∞—Ç–∏–∫–∞')}
            className={
              quizValues.current?.difficulties === '–ì—Ä–∞–º–∞—Ç–∏–∫–∞' && 'chosen'
            }
          >
            <QuizButtonContent>
              <Emoji src={brain} alt="Brain explosion emoji" width="21" />
              –ì—Ä–∞–º–∞—Ç–∏–∫–∞
            </QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, '–°–ø—ñ–ª–∫—É–≤–∞–Ω–Ω—è')}
            className={
              quizValues.current?.difficulties === '–°–ø—ñ–ª–∫—É–≤–∞–Ω–Ω—è' && 'chosen'
            }
          >
            <QuizButtonContent>
              <Emoji src={speak} alt="Speaking emoji" width="21" />
              –°–ø—ñ–ª–∫—É–≤–∞–Ω–Ω—è
            </QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, '–†–æ–∑—É–º—ñ–Ω–Ω—è –º–æ–≤–∏ –Ω–∞ —Å–ª—É—Ö')}
            className={
              quizValues.current?.difficulties === '–†–æ–∑—É–º—ñ–Ω–Ω—è –º–æ–≤–∏ –Ω–∞ —Å–ª—É—Ö' &&
              'chosen'
            }
          >
            <QuizButtonContent>
              <Emoji src={ear} alt="Ear emoji" width="21" />
              –†–æ–∑—É–º—ñ–Ω–Ω—è –º–æ–≤–∏ –Ω–∞ —Å–ª—É—Ö
            </QuizButtonContent>
          </QuizButton>
        </QuizButtonBox>
        <BackgroundFilterTopRight />
        <BackgroundFilterBottomLeft />
        <BackgroungStarSmall />
        <Pagination>
          <PreviousPageBtn
            className={activeSlide - 1 < 1 && 'disabled'}
            disabled={activeSlide - 1 < 1 && true}
            onClick={previousQuestion}
          >
            <QuizArrowLeft />
          </PreviousPageBtn>
          <PageCounter>
            <CurrentPage>{activeSlide}</CurrentPage>/{lang ? 7 : 8}
          </PageCounter>
          <NextPageBtn
            className={
              activeSlide + 1 > 1 &&
              !quizValues.current?.difficulties &&
              'disabled'
            }
            disabled={
              activeSlide + 1 > 1 && !quizValues.current?.difficulties && true
            }
            onClick={nextQuestion}
          >
            <QuizArrowRight />
          </NextPageBtn>
        </Pagination>
      </QuizBox>
    </>
  );
};
